import React, { useEffect, useState } from 'react';
import './App.css';
import List from './components/List';
import withListLoading from './components/withListLoading';
function App() {
  const ListLoading = withListLoading(List);
  const [appState, setAppState] = useState({
    loading: false,
    repos: null,
  });

  useEffect(() => {
    setAppState({ loading: true });
    const apiUrl = "https://bondpa.se/skane/json";
    fetch(apiUrl)
      .then((res) => res.json())
      .then((departures) => {
        setAppState({ loading: false, departures: departures });
      });
  }, [setAppState]);
  return (
    <div className='App'>
      <div className="jumbotron jumbotron-fluid">
        <div className="container-fluid">
          <h1 className="display-4">Avgående trafik från Triangeln</h1>
          <p className="lead">Denna applikation hämtar sin data från Skånetrafikens api.</p>
        </div>
      </div>

      <div className='departure-container'>
        <ListLoading isLoading={appState.loading} departures={appState.departures} />
      </div>
    </div>
  );
}
export default App;
