import React from "react"; 

const List = (props) => {
    const { departures } = props;
    if (!departures || departures.length === 0) return <p>Inga avgångar tillgängliga</p>;
    return (
        <div class="row">
            <table class="table">
                <thead>
                    <tr>
                        <th scope="col">Tid</th>
                        <th scope="col">Linje</th>
                        <th scope="col">Nummer</th>
                        <th scope="col">Mot</th>
                    </tr>
                </thead>
                <tbody>
                {departures.map((departure) => {
                    return (
                        <tr key={departure.id} className="list">
                            <td className="departure-time">{departure.journeyDateTime} </td>
                            <td className="departure-name">{departure.name} </td>
                            <td className="departure-no">{departure.no} </td>
                            <td className="departure-towards">{departure.towards}</td>
                        </tr>
                    );
                })}
                </tbody>
            </table>
        </div>
    );
};

export default List;